import React, { useState, lazy } from "react";
import { withTranslation, Trans } from "react-i18next";
import {
  Grid,
  Typography,
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Container,
  Box,
} from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import bottomImage from "../assets/footer.svg";
const Footer = lazy(() => import("./Footer"));
const NewsletterComponent = lazy(() => import("./NewsletterComponent"));

const faqs = [
  {
    id: 1,
    question: <Trans>{"faqComponent.question1.question"}</Trans>,
    answer: <Trans>{"faqComponent.question1.answer"}</Trans>,
  },
  {
    id: 2,
    question: <Trans>{"faqComponent.question2.question"}</Trans>,
    answer: <Trans>{"faqComponent.question2.answer"}</Trans>,
  },
  {
    id: 3,
    question: <Trans>{"faqComponent.question3.question"}</Trans>,
    answer: <Trans>{"faqComponent.question3.answer"}</Trans>,
  },
  {
    id: 4,
    question: <Trans>{"faqComponent.question4.question"}</Trans>,
    answer: <Trans>{"faqComponent.question4.answer"}</Trans>,
  },
  {
    id: 5,
    question: <Trans>{"faqComponent.question5.question"}</Trans>,
    answer: <Trans>{"faqComponent.question5.answer"}</Trans>,
  },
  {
    id: 6,
    question: <Trans>{"faqComponent.question6.question"}</Trans>,
    answer: <Trans>{"faqComponent.question6.answer"}</Trans>,
  },
];

const FaqComponent = ({ t }) => {
  const [isExpanded, setIsExpanded] = useState("");

  const handleChange = (question) => {
    setIsExpanded(isExpanded === question ? false : question);
  };

  return (
    <Box mt={-12}>
      <NewsletterComponent />
      <Container
        maxWidth="100%"
        sx={{
          display: "flex",
          flexDirection: "column",
          justifyContent: "space-around",
          alignItems: "center",
          background: `transparent url(${bottomImage}) no-repeat center top`,
          backgroundSize: "cover",
        }}
      >
        <Grid
          container
          mt="100vh"
          flexDirection="column"
          justifyContent="center"
          alignContent="flex-end"
          sx={{
            "@media (max-width:1700px)": {
              marginTop: "100vh",
            },
            "@media (max-width:900px)": {
              marginTop: "160vh",
            },
          }}
        >
          <Grid item>
            <Typography variant="h2" color={"secondary.main"} mb={5}>
              {t("faqComponent.title")}
            </Typography>
          </Grid>
          <Grid container item justifyContent="center">
            <Grid
              container
              item
              xs={12}
              pb="10vh"
              spacing={3}
              justifyContent="center"
              px={0.3}
            >
              {faqs.map((faq) => (
                <Grid item xs={12} md={5} key={`faq:${faq.id}`}>
                  <Accordion
                    square
                    expanded={isExpanded === faq.question}
                    onChange={() => handleChange(faq.question)}
                  >
                    <AccordionSummary
                      expandIcon={
                        <ExpandMoreIcon
                          sx={{
                            color: "secondary.main",
                            opacity: "0.3",
                            fontSize: "2.5rem",
                          }}
                        />
                      }
                    >
                      <Typography
                        variant="h4"
                        textAlign="left"
                        color="common.white"
                      >
                        {faq.question}
                      </Typography>
                    </AccordionSummary>
                    <AccordionDetails sx={{ textAlign: "justify" }}>
                      <Typography
                        variant="h5"
                        color="grey.500"
                        textAlign="left"
                        mt={-1.5}
                      >
                        {faq.answer}
                      </Typography>
                    </AccordionDetails>
                  </Accordion>
                </Grid>
              ))}
            </Grid>
          </Grid>
        </Grid>
        <Footer />
      </Container>
    </Box>
  );
};
export default withTranslation()(FaqComponent);
